class Cokolwiek {
    public name: String;
    public surname: String;
    public age: Number;
    public height: Number;
    constructor(name, surname, age, height){
        this.name = name;
        this.surname = surname;
        this.age = age;
        this.height = height;
    }
}
let x = new Cokolwiek('janusz', 'andrzej',24, 188);
console.log(x.name);
console.log(x.age);


